@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');
* {
    margin: 0;
    padding: 0;
}
body {
    height: 100%;
    color: #333;
}

/* 
    FEF9A7 - light yellow
    FAC213 - yellow
    F77E21 - orange
    D61C4E - red
*/

.App {
    height: 100vh;
    background-color: #fef9a7;
    overflow: hidden;
}
.header {
    height: 60px;
    padding-top: 30px;
    line-height: 60px;
    text-align: center;
    font-size: 35px;
    font-family: 'Indie Flower', cursive;
}
.drag-area {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
}
.rect {
    width: 80px;
    height: 80px;
    position: absolute;
    top: 150px;
}
.rect1 {
    background-color: #f77e21;
    left: 300px;
}
.rect2 {
    width: 100px;
    height: 100px;
    background-color: #d61c4e;
    left: 800px;
}
.hidden {
    display: none;
}
